.navbar{
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    background: #1C307A; opacity: 0.9;
    height: 137px;
}

.navbar-navigator{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    flex: 1 1;
}

.navbar-logo{
    margin-left: 0.5rem;
}

.navbar-logo a{
    padding: 0 10%;
}

.navbar-items-spacer{
    flex: 1 1;
}

.navbar-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.navbar-items li{
    padding: 0 3rem;
}

.navbar-items a{
    color: #ABC5ED;
    font-family: "Barlow", sans-serif;
    font-size: 24px;
    font-weight: bold;
    text-decoration: none;
}

.navbar-items a:hover,
.navbar-items a:active{
    color: #f9aa00;
    text-decoration: none;
}

